	import {  
        mapMutations  
    } from 'vuex';
	
	export default{
		data(){
			return {
				phone: '',
				captcha: '',
				logining: false
			}
		},
		onLoad(){
			
		},
		methods: {
			...mapMutations(['login']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack();
			},
			toLogin(){
 				uni.navigateTo({
					url:"/pages/public/login"
				})
			},
			async toReg(){
				this.logining = true;
				const {phone, captcha} = this;
				/* 数据验证模块
				if(!this.$api.match({
					mobile,
					password
				})){
					this.logining = false;
					return;
				}
				*/ 
				var _this = this;
				this.sendRequest({
					url : "reg",
					data : {phone : phone,captcha:captcha},
					method : "post",
 					success:function (res) {
 						if(res.code == '200'){
							//如果是app的登录
							_this.$api.msg(res.message); 
							uni.setStorageSync(_this.sessionKey,res.data.token);
							_this.login(res.data);
							uni.navigateBack({  //uni.navigateTo跳转的返回，默认1为返回上一级
								delta: 2
							});
						}else{
							_this.$api.msg(res.message);
							_this.logining = false;
						} 
					}
				});  
			}
		},

	}
